import "../scss/enroll.scss";

const pwd = document.getElementById("pwd");
const pwd2 = document.getElementById("pwd2");
const email = document.getElementById("email");
const phone = document.getElementById("phone")
const pattern_pwd = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,20}$/;
const pattern_phone = /^09[0-9]{8}$/;
const pattern_email = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;


email.addEventListener('input', () => {
    const existingWarning = document.querySelector('#email-warning');
    if (existingWarning) {
        existingWarning.remove();
    }

    if (!pattern_email.test(email.value)) { 
        const warning = document.createElement('p');
        warning.textContent = "請輸入有效的信箱格式";
        warning.id = 'email-warning'; 
        warning.style.color = 'red'; 
        email.parentNode.appendChild(warning); 
    }
});

pwd.addEventListener('input', () => {
    const existingWarning = document.querySelector('#pwd-warning');
    if (existingWarning) {
        existingWarning.remove();
    }

    if (!pattern_pwd.test(pwd.value)) {
        const warning = document.createElement('p');
        warning.textContent = "密碼請包含英文數字，6~20位數";
        warning.id = 'pwd-warning'; 
        warning.style.color = 'red'; 
        pwd.parentNode.appendChild(warning); 
    }
});

pwd2.addEventListener('input', () => {
    const existingWarning = document.querySelector('#pwd2-warning');
    if (existingWarning) {
        existingWarning.remove();
    }

    if (pwd.value !== pwd2.value) {
        const warning = document.createElement('p');
        warning.textContent = "密碼不一致";
        warning.id = 'pwd2-warning'; 
        warning.style.color = 'red'; 
        pwd2.parentNode.appendChild(warning); 
    }
});

phone.addEventListener('input', () => {
    const existingWarning = document.querySelector('#phone-warning');
    if (existingWarning) {
        existingWarning.remove();
    }

    if (!pattern_phone.test(phone.value)) {
        const warning = document.createElement('p');
        warning.textContent = "請輸入正確的手機號碼";
        warning.id = 'phone-warning'; 
        warning.style.color = 'red'; 
        phone.parentNode.appendChild(warning); 
    }
});


