one;\n            color: #3b3b3b;\n            cursor: pointer;\n            outline: none;\n            padding: 16px 24px;\n            text-align: center;\n            text-decoration: none;\n            user-select: none;\n            -webkit-user-select: none;\n            width: fit-content;\n            align-self: center;\n        }\n\n        a:disabled {\n            pointer-events: none;\n        }\n\n        a:hover {\n            color: #fff;\n            background-color: #1a1a1a;\n            box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n            border-radius: 15px;\n        }\n\n        a:active {\n            box-shadow: none;\n        }\n    }\n}\n\n.content {\n    form{\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n        gap: 2rem;\n        margin-top: 10vh;\n        min-height: 90vh;\n        margin-bottom: 5vh;\n\n        hr{\n            width: 60%;\n            margin: 1rem;\n        }\n\n        h2{\n            @include h2();\n        }\n        .login{\n            width: 60vw;\n            display: flex;\n            justify-content: left;\n            align-content: center;\n            flex-wrap: wrap;\n            margin-left: 8rem;\n            gap: 1rem;\n            label{\n                @include text(1.5rem);\n                width: fit-content;\n            }    \n            input{\n                @include noto-sans-tc();\n                color: #000000;\n                font-size: 1.5rem;\n                font-weight: 1rem;\n                height: 1.5rem;\n                text-align: center;\n                padding: 16px;\n            }    \n            p{\n                @include text(1.5rem);\n            }\n        }\n        .info{\n            width: 60vw;\n            display: flex;\n            justify-content: left;\n            align-content: center;\n            flex-wrap: wrap;\n            margin-left: 8rem;\n            gap: 1rem;\n            label{\n                @include text(1.5rem);\n                width: fit-content;\n            }    \n            input{\n                @include noto-sans-tc();\n                color: #000000;\n                font-size: 1.5rem;\n                font-weight: 1rem;\n                height: 1.5rem;\n                text-align: center;\n                padding: 16px;\n            }    \n            p{\n                @include text(1.5rem);\n            }\n        }\n        #submit{\n            @include noto-sans-tc();\n            color: #000000;\n            font-size: 1.2rem;\n            font-weight: 1rem;\n            width: 6rem;\n            height: 4rem;\n            text-align: center;\n            cursor: pointer;\n            color: #fff;\n            background-color: #1a1a1a;\n            border-radius: 15px;\n            &:hover{\n                background: linear-gradient(to bottom, $main-theme-color, #3b3b3b);\n                font-size: 1.5rem;\n                box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n            }\n        }  \n        a{\n            @include text(1rem);\n            text-decoration: none;\n        }  \n        a:disabled {\n            pointer-events: none;\n        }\n        a:hover {\n            color: #fff;\n            background-color: #1a1a1a;\n            box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n            border-radius: 15px;\n        }\n\n        a:active {\n            box-shadow: none;\n        }\n    }\n}\n\n.footer{\n    display: flex;\n    justify-content: left;\n    align-items: center;\n    height: 10vh;\n    box-shadow: rgba(0, 0, 0, 0.25) 0px -5px 5px;\n    overflow: hidden;\n    #title{\n        @include textGradient($main-theme-color);\n        font-size: 2.5rem;\n        font-weight: 800;\n        width: 30%;\n        height: 8vh;\n        text-align: center;\n        text-wrap: nowrap;\n    }\n    .info{\n        font-size: 1.2rem;\n        font-weight: 500;\n        width: 30%;\n        height: fit-content;\n        text-align: left;\n        text-wrap: nowrap;\n\n        @include noto-sans-tc();\n        color: #000000;\n    }\n     a#footer-link {\n        margin-left: 20%;\n        @include noto-sans-tc();\n        font-size: 1.2rem;\n        appearance: none;\n        background-color: transparent;\n        border: none;\n        color: #3b3b3b;\n        cursor: pointer;\n        outline: none;\n        padding: 16px 24px;\n        text-align: center;\n        text-decoration: none;\n        user-select: none;\n        -webkit-user-select: none;\n        width: fit-content;\n        align-self: center;\n    }\n\n   a:disabled#footer-link {\n        pointer-events: none;\n    }\n\n    a:hover#footer-link {\n        color: #fff;\n        background-color: #1a1a1a;\n        box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;\n        border-radius: 15px;\n    }\n\n    a:active#footer-link {\n        box-shadow: none;\n    }\n}"],"sourceRoot":""}]);
